html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
}
body.fijar-body {
  overflow: hidden;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

p {
  font-size: 2rem;
  color: #1E1E1E;
}

h1 {
  font-size: 5rem;
}

h2 {
  font-size: 4rem;
}
@media (max-width: 1200px) {
  h2 {
    font-size: 3.2rem;
  }
}

img {
  max-width: 100%;
  height: 90%;
}

.contenedor-header {
  width: 80%;
  height: 100%;
  max-width: 145rem;
  margin: 0 auto;
  border-bottom: 1px solid;
  border-radius: 1.4%;
}

.contenido-header {
  width: 80%;
  max-width: 120rem;
  margin: 0 auto;
}

.header {
  height: 9.5rem;
  font-family: "Bebas Neue", cursive;
  text-transform: uppercase;
  color: white;
  width: 100%;
  top: 0;
  left: 0;
  margin-bottom: 7.5rem;
  position: fixed;
  background-color: #1E1E1E;
}
@media (max-width: 920px) {
  .header {
    height: 12rem;
  }
}

.navegacion {
  display: flex;
  justify-content: space-between;
  height: 9.5rem;
}
@media (max-width: 920px) {
  .navegacion {
    flex-direction: column;
    align-items: center;
  }
}

.navegacion__link {
  color: white;
  text-shadow: 0px 1px 1px #000000;
  text-decoration: none;
  padding: 0;
  height: 0;
}

.titulo__header {
  font-size: 6.5rem;
  line-height: 12.5rem;
  color: #00ADB5;
}
@media (max-width: 920px) {
  .titulo__header {
    line-height: 11rem;
  }
}
@media (max-width: 1024px) {
  .titulo__header {
    font-size: 6rem;
  }
}

.navegacion__menu {
  display: flex;
  gap: 6rem;
  padding: 0;
  list-style: none;
}
@media (max-width: 920px) {
  .navegacion__menu {
    margin-top: 4rem;
  }
}
@media (max-width: 1200px) {
  .navegacion__menu {
    gap: 3rem;
  }
}

.navegacion__menu__item {
  font-size: 2.7rem;
  line-height: 11.5rem;
  padding: -1rem;
}
@media (max-width: 1024px) {
  .navegacion__menu__item {
    font-size: 2.5rem;
  }
}

.navegacion__menu__enlace:hover {
  color: #1E1E1E;
  transition: 0.7s;
  padding: 0;
}

.navegacion__toggle {
  background: none;
  border: none;
  line-height: 15.5rem;
  display: none;
}

@media (max-width: 480px) {
  .header {
    height: 8.5rem;
  }
  .titulo__header {
    font-size: 5rem;
    left: 5rem;
    position: absolute;
  }
  .navegacion__menu {
    flex-direction: column;
    padding-top: 3rem;
    background-color: #00ADB5;
    color: #1E1E1E;
    padding: 5rem;
    gap: 7rem;
    position: fixed;
    left: 0;
    top: 4.4rem;
    width: 100%;
    height: 100%;
    overflow-y: auto;
    border-top: 2px solid white;
    border-radius: 1.4%;
    left: 100%;
    transition: left 0.3s;
  }
  .navegacion__menu__item {
    display: flex;
    justify-content: center;
    align-items: center;
    line-height: 1rem;
    height: 0;
  }
  .navegacion__menu__active {
    color: #1E1E1E;
  }
  .navegacion__toggle {
    display: block;
    right: 5rem;
    position: absolute;
    line-height: 13.5rem;
  }
  .navegacion__menu__visible {
    left: 0;
  }
  .navegacion__menu__esconder {
    left: 1;
  }
}
.contenedor-frase {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}
@media (max-width: 480px) {
  .contenedor-frase {
    width: 85%;
  }
}
@media (max-width: 920px) {
  .contenedor-frase {
    width: 80%;
  }
}

.frase {
  margin-top: 9.5rem;
  background-color: #1E1E1E;
  padding: 1.5rem;
}

.frase-header {
  text-align: center;
  font-family: "Nunito Sans", sans-serif;
  font-size: 1.4rem;
  text-transform: none;
  color: white;
}
@media (max-width: 920px) {
  .frase-header {
    font-size: 1.2rem;
  }
}
@media (max-width: 480px) {
  .frase-header {
    font-size: 1.1rem;
  }
}

.contenedor {
  width: 95%;
  max-width: 160rem;
  margin: 0 auto;
}

.contenido-principal {
  width: 100%;
  background-color: #EEEEEE;
  text-align: center;
  display: flex;
  align-items: center;
}

.secciones {
  width: 95%;
  gap: 3rem;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 10rem;
  margin-top: 8rem;
  margin-bottom: 8rem;
}
@media (max-width: 1200px) {
  .secciones {
    gap: 5rem;
  }
}
@media (max-width: 920px) {
  .secciones {
    margin-top: 6rem;
    margin-bottom: 6rem;
  }
}
@media (max-width: 480px) {
  .secciones {
    padding: 0 10px 0 10px;
  }
}

.secciones video {
  width: 100%;
}

.texto-pregunta {
  font-family: "Bebas Neue", cursive;
  font-size: 3.2rem;
  color: #393E46;
  margin: 0;
}
@media (min-width: 1200px) {
  .texto-pregunta {
    font-size: 4rem;
  }
}

.texto-pregunta span {
  color: #00ADB5;
}

.texto-descripcion {
  font-family: "Nunito Sans", sans-serif;
  font-size: 1.6rem;
  font-weight: 200;
}
@media (min-width: 1200px) {
  .texto-descripcion {
    font-size: 1.9rem;
  }
}

.descripcion {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.escenarios { /**NOMBRE DEL SECTION*/
  padding: 6rem 0;
}
.escenarios h2, .escenarios p {
  text-align: center;
}
.escenarios .titulo-escenarios {
  font-family: "Bebas Neue", cursive;
  color: white;
  text-shadow: 1px 1px 1px #000000;
}
.escenarios .dia {
  font-family: "Bebas Neue", cursive;
  text-transform: uppercase;
  color: white;
  padding: 2.9rem 0 2.9rem 0;
}

.escenario-contenedor {
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 10rem;
  flex-wrap: wrap;
  padding-bottom: 5rem;
}
@media (max-width: 844px) {
  .escenario-contenedor {
    gap: 5rem;
  }
}

/**CODIGO DE LAS TAREJTAS DE LOS ESCENARIO**/
.escenario {
  text-align: center;
  border-radius: 2rem;
  padding-top: 1.5rem;
  width: 35rem;
}
@media (max-width: 920px) {
  .escenario {
    width: 30rem;
  }
}
.escenario .nombre-escenario {
  color: #393E46;
}
.escenario .nombre-escenario:after {
  content: "";
  display: block;
  width: 20rem;
  height: 10rem;
  background-size: 7rem;
  background-repeat: no-repeat;
  background-position: center center;
  margin: 2.5rem auto 2.5rem auto;
}

.pacMan .nombre-escenario:after {
  background-image: url(../img/pacman.png);
}

.cosplayer .nombre-escenario:after {
  background-image: url(../img/cosplayer.png);
}

.anime .nombre-escenario:after {
  background-image: url(../img/anime.png);
}

.nombre-escenario {
  font-family: "Lato", sans-serif;
  font-weight: 600;
  text-shadow: 0px 1px 1px white;
  font-size: 2rem;
}
@media (max-width: 1024px) {
  .nombre-escenario {
    font-size: 1.5rem;
  }
}

.calendario {
  font-family: "Nunito Sans", sans-serif;
  font-weight: bold;
  list-style: none;
  color: #00ADB5;
  padding: 0;
}
.calendario span {
  font-weight: 200;
  color: #1E1E1E;
}
.calendario li {
  background-color: #D0D0D0;
  padding: 1.5rem;
  margin-bottom: 4.3rem;
}
@media (max-width: 1024px) {
  .calendario {
    font-size: 1.4rem;
  }
}

.fondo {
  background-color: #EEEEEE;
}

.galeria {
  background-color: #EEEEEE;
  padding: 6rem 0 10rem;
  text-align: center;
}

.galeria-imagenes {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 480px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 920px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 1024px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 2rem;
  }
}

.titulo-galeria {
  padding-bottom: 2.5rem;
  font-family: "Bebas Neue", cursive;
  color: #393E46;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: grid;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.3);
}
.overlay picture {
  display: block;
  max-width: 90rem;
  text-align: center;
  padding: 2rem;
}

.boton-cerrar {
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  background-color: white;
  color: #00ADB5;
  font-family: "Lato", sans-serif;
  font-weight: bold;
  font-size: 2.3rem;
  display: grid;
  place-items: center;
}
.boton-cerrar:hover {
  cursor: pointer;
  color: white;
  background-color: #00ADB5;
}

