@use 'base/variables' as v;//SE IMPORTA EL ARCHIVO variables PARA QUE SE PUEDA USAR DENTRO DE ESTE ARCHIVO
@use 'base/mixins' as m; //SE IMPORTA PARA PODER USAR LOS MIXINS

//HEADER

.contenedor-header {
    width: 80%;
    height: 100%;
    max-width: 145rem;
    margin: 0 auto;

    border-bottom: 1px solid;
    border-radius: 1.4%;
}

.contenido-header {
    width: 80%;
    max-width: 120rem;
    margin: 0 auto;
}

.header {
    height: 9.5rem;

    font-family: v.$letraTitulo1;
    text-transform: uppercase;
    color: v.$letraBlanca;
    width: 100%;
    top: 0;
    left: 0;
    margin-bottom: 7.5rem;
    position: fixed;
    background-color: v.$principal;

    @include m.tabletMax {
        height: 12rem;
    }
}

.navegacion {
    display: flex;
    justify-content: space-between;
    height: 9.5rem;

    @include m.tabletMax {
        flex-direction: column;
        align-items: center;
    }
}

.navegacion__link {
    color: v.$letraBlanca;
    text-shadow: 0px 1px 1px v.$letraNegra;
    text-decoration: none;
    padding: 0;
    height: 0;
}

.titulo__header {
    font-size: 6.5rem;
    line-height: 12.5rem;
    color: v.$terciario;

    @include m.tabletMax {
        line-height: 11rem;
    }

    @include m.desktopMax {
        font-size: 6rem;
    }
}

.navegacion__menu {
    display: flex;
    gap: 6rem;
    padding: 0;

    list-style: none;

    @include m.tabletMax {
        margin-top: 4rem;
    }

    @include m.desktopXLMax {
        gap: 3rem;
    }
}

.navegacion__menu__item {
    font-size: 2.7rem;
    line-height: 11.5rem;
    padding: -1rem;

    @include m.desktopMax {
        font-size: 2.5rem;
    }
}

.navegacion__menu__enlace:hover {
    color: v.$principal;
    transition: 0.7s;
    padding: 0;
}

//CÓDIGO PARA EL TITULO

.navegacion__toggle {
    background: none;
    border: none;

    line-height: 15.5rem;

    display: none;
}

@include m.phoneMax {
    .header {
        height: 8.5rem;
    }

    .titulo__header {
        font-size: 5rem;
        left: 5rem;
        position: absolute;
    }

    .navegacion__menu {
        flex-direction: column;
        padding-top: 3rem;
        background-color: v.$terciario;
        color: v.$principal;
        padding: 5rem;
        gap: 7rem;
        position: fixed;
        left: 0;
        top: 4.4rem;
        width: 100%;

        height: 100%;
        overflow-y: auto;

        border-top: 2px solid v.$letraBlanca;
        border-radius: 1.4%;

        left: 100%;
        transition: left 0.3s;
    }

    .navegacion__menu__item {
        display: flex;
        justify-content: center;
        align-items: center;
        line-height: 1rem;
        height: 0;
    }

    .navegacion__menu__active {
        color: v.$principal;
    }

    .navegacion__toggle {
        display: block;
        right: 5rem;
        position: absolute;
        line-height: 13.5rem;
    }

    .navegacion__menu__visible {
        left: 0;
    }

    .navegacion__menu__esconder {
        left: 1;
    }
}