@use 'base/variables' as v;
@use 'base/mixins' as m;

.boletos {
    padding: 5rem 0 10rem 0;

    p, h2 {
        color: v.$letraBlanca;
    }

    h2 {
        text-align: center;
    }
}

.titulo-boletos {
    font-family: v.$letraTitulo1    ;
}

.contenedor-boletos {
    place-items: center;

    @include m.tablet {
        @include m.grid(2, 5rem);
    }
}

.normal, .vip {
    @include m.tabletMax {
        padding: 1.5rem;
    }
}

.titulo-pase {
    font-family: v.$letraTitulo2;
    text-align: center;
    font-size: 3rem;
    font-weight: 300;
    margin: 0;
    padding: 3rem 0;
}

.pase {

    &-header,
    &-footer {
        padding: 0.5rem;

        p {
            @include m.resetear-lista;
            text-align: center;
            text-transform: uppercase;
            font-family: v.$letraTitulo1;
        }
    }

    &-header{
        background-color: v.$secundario;
        padding: 1rem;
        p {
            font-size: 1.7rem;
            font-family: v.$letraContenido;
            font-weight: 500;
        }
    }

    &-body {
        font-family: v.$letraContenido;
        font-size: 1.5rem;

        ul {
            @include m.resetear-lista;
        }

        li {
            background-color: v.$fondo-horario;
            padding: 1rem;
            border-bottom: 1px solid v.$letraGris  ;
            

            :last-of-type{
                border-bottom: none;
            }
        }
    }
    
    &-footer {
        background-color: v.$terciario;
        p {
            font-size: 3rem;
        }
    }
}