@use 'base/variables' as v;
@use 'base/mixins' as m;

.escenarios { /**NOMBRE DEL SECTION*/
    padding: 6rem 0;

    h2, p {
        text-align: center;
    }

    .titulo-escenarios {
        font-family: v.$letraTitulo1;
        color: v.$letraBlanca;
        text-shadow: 1px 1px 1px v.$letraNegra;
    }
    
    .dia {
        font-family: v.$letraTitulo1;
        text-transform: uppercase;
        color: v.$letraBlanca;
        padding: 2.9rem 0 2.9rem 0;
    }
}

.escenario-contenedor {
    display: flex;
    flex-direction: row;
    justify-content: center;
    gap: 10rem;
    flex-wrap: wrap;
    padding-bottom: 5rem;

    @include m.brakeCalendarioMax {
        gap: 5rem;
    }
}

/**CODIGO DE LAS TAREJTAS DE LOS ESCENARIO**/

.escenario {
    text-align: center;
    border-radius: 2rem;
    padding-top: 1.5rem;
    width: 35rem;

    @include m.tabletMax {
        width: 30rem;
    }

    .nombre-escenario {

        color: v.$secundario;
        &:after {
            content: '';
            display: block;
            width: 20rem;
            height: 10rem;
            background-size: 7rem;
            background-repeat: no-repeat;
            background-position: center center;
            margin: 2.5rem auto 2.5rem auto;    
        }
    }
}

.pacMan .nombre-escenario:after {
    background-image: url(../img/pacman.png);
}

.cosplayer .nombre-escenario:after {
    background-image: url(../img/cosplayer.png);
}

.anime .nombre-escenario:after {
    background-image: url(../img/anime.png);
}

.nombre-escenario {
    font-family: v.$letraTitulo2;
    font-weight: 600;
    text-shadow: 0px 1px 1px v.$letraBlanca;
    font-size: 2rem;

    @include m.desktopMax {
        font-size: 1.5rem;
    }
}

.calendario {
    font-family: v.$letraContenido;
    font-weight: bold;
    list-style: none;
    color: v.$terciario;
    padding: 0;

    span {
        font-weight: 200;
        color: v.$principal;
    }

    li {
        background-color: v.$fondo-horario;
        padding: 1.5rem;
        margin-bottom: 4.3rem;
    }

    @include m.desktopMax {
        font-size: 1.4rem;
    }
}

.fondo {
        background-color: v.$fondos-contraste;
}

