@use 'variables' as v;
@use 'mixins' as m;

html {
    font-size: 62.5%; //Esto nos facilita de que 1rem sea igual a 1px
    box-sizing: border-box;
}

*,
*::before,
*::after {
    box-sizing: inherit; //Para que el pading no sea un problema
}

body {
    font-size: 1.6rem;

    &.fijar-body {
        overflow: hidden;
    }
}

.contenedor {
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
}

p {
    font-size: 2rem;
    color: v.$principal;
}

h1 {
    font-size: 5rem; //PARA QUE TODOS LOS H1 ESTÉN DEL TAMAÑO DEL TITULO DEL HEADER DE LA PÁGINA
}

h2 {
    font-size: 4rem;

    @include m.desktopXLMax {
        font-size: 3.2rem;
    }
}

img {
    max-width: 100%;
    height: 90%;
}