@use 'base/variables' as v;//SE IMPORTA EL ARCHIVO variables PARA QUE SE PUEDA USAR DENTRO DE ESTE ARCHIVO
@use 'base/mixins' as m; //SE IMPORTA PARA PODER USAR LOS MIXINS

.contenedor-frase {
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;

    @include m.phoneMax {
        width: 85%;
    }

    @include m.tabletMax {
        width: 80%;
    }
}
.frase {
    margin-top: 9.5rem;
    background-color: v.$principal;
    padding: 1.5rem;
    
}

.frase-header {
    text-align: center;
    font-family: v.$letraContenido;
    font-size: 1.4rem;
    text-transform: none;
    color: v.$letraBlanca;

    @include m.tabletMax {
        font-size: 1.2rem;
    }

    @include m.phoneMax {
        font-size: 1.1rem;
    }
}

.contenedor{
    width: 95%;
    max-width: 160rem;
    margin: 0 auto;
}

.contenido-principal {
    width: 100%;
    background-color: v.$fondos-contraste;
    text-align: center;
    display: flex;
    align-items: center;
}

.secciones {
    width: 95%;
    gap: 3rem;
    display: grid;
    grid-template-columns: repeat(
        auto-fit,
        minmax(
            320px,
            1fr
        )
    );
    gap: 10rem;
    margin-top: 8rem;
    margin-bottom: 8rem;

    @include m.desktopXLMax {
        gap: 5rem;
    }

    @include m.tabletMax {
        margin-top: 6rem;
        margin-bottom: 6rem;
    }

    @include m.phoneMax {
        padding: 0 10px 0 10px;
    }

}

.secciones video {
    width: 100%;
}

.texto-pregunta {
    font-family: v.$letraTitulo1;
    font-size: 3.2rem;
    color: v.$secundario;
    margin: 0;

    @include m.desktopXL {
        font-size: 4rem;
    }
}

.texto-pregunta span {
    color: v.$terciario;
}

.texto-descripcion {
    font-family: v.$letraContenido;
    font-size: 1.6rem;
    font-weight: 200;

    @include m.desktopXL {
        font-size: 1.9rem;
    }

}

.descripcion {
    display: flex;
    flex-direction: column;
    justify-content: center;
}