@use 'base/variables' as v;
@use 'base/mixins' as m;

.galeria {
    background-color: v.$fondos-contraste;
    padding: 6rem 0 10rem;
    text-align: center;
}

.galeria-imagenes {
    @include m.resetear-lista;
    @include m.grid(2, 2rem);

    @include m.phone {
        @include m.grid(3, 2rem);
    }
    @include m.tablet {
        @include m.grid(4, 2rem);
    }
    @include m.desktop {
        @include m.grid(6, 2rem);
    }
}

.titulo-galeria {
    padding-bottom: 2.5rem;
    font-family: v.$letraTitulo1;
    color: v.$secundario;
}

.overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    display: grid;
    place-items: center;

    background-color: rgb(0 0 0 / 0.3);

    picture {
        display: block;
        max-width: 90rem;
        text-align: center;
        padding: 2rem;
    }
}

.boton-cerrar {
    width: 4rem;
    height: 4rem;
    border-radius: 50%;
    background-color: v.$letraBlanca;
    color: v.$terciario;
    font-family: v.$letraTitulo2;
    font-weight: bold;
    font-size: 2.3rem;
    display: grid;
    place-items: center;

    &:hover {
        cursor: pointer;
        color: v.$letraBlanca;
        background-color: v.$terciario;
    }
}