    padding: 0.5rem;
    text-align: center;
    p{
        font-family: v.$letraTitulo2;
        font-size: 1rem;
        font-size: 1.3rem;
        font-weight: 400;

        span {
            color: v.$terciario;
            text-transform: uppercase;
        }
    }
}