@use 'variables' as v;

@mixin phone {
    @media (min-width: v.$celular) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}

@mixin desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
}

@mixin desktopXL {
    @media (min-width: v.$desktopXL) {
        @content;
    }
}

@mixin desktopXXL {
    @media (min-width: v.$desktopXL) {
        @content;
    }
}

//MAXIMO

@mixin phoneMax {
    @media (max-width: v.$celular) {
        @content;
    }
}

@mixin tabletMax {
    @media (max-width: v.$tablet) {
        @content;
    }
}

@mixin desktopMax {
    @media (max-width: v.$desktop) {
        @content;
    }
}

@mixin desktopXLMax {
    @media (max-width: v.$desktopXL) {
        @content;
    }
}

@mixin desktopXXLMax {
    @media (max-width: v.$desktopXXL) {
        @content;
    }
}

@mixin brakeCalendarioMax {
    @media (max-width: v.$brakeCalendario) {
        @content;
    }
}

//GRID

@mixin grid($columnas, $espaciado) {
    display: grid;
    grid-template-columns: repeat($columnas, 1fr);
    gap: $espaciado;
}

@mixin  resetear-lista {
    margin: 0;
    padding: 0;
    list-style: none;
}